import React, { useEffect } from "react";
import Ctablock from "../components/Ctablock";

import InnerBanner from "../components/InnerBanner";
import InfoImg from "../images/info-sec-img.png";
import { Dapps_Data } from "../data/all-data";

const DAppsDevelopment = () => {
  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, []);
  return (
    <>
      <InnerBanner
        title="DApps Development"
        desc="
We build cryptocurrency wallet development services with security features and a user-friendly interface for users that matches their business model.
"
      />

      <section className="infosec-wrap section gray-bg">
        <div className="container">
          <div className="row">
            <div className="col-lg-7">
              <div className="content-block">
                <h2 className="h2 title mb-4">Globalize Your Products</h2>
                <p className="desc">
                  A global trend is arising in the form of DApps Blockchain
                  mobile applications. Technology has itself started the
                  revolution in all niches including Finance, Healthcare,
                  Education, Information Technology, and more.
                </p>
                <p className="desc">
                  DAO Platforms believes that you can get a chance of this hype
                  train to build up your business, not only that we are here to
                  help you with anything related to DApps Development. You can
                  globalize your idea, products, and brand just minutes away by
                  letting us do the hard work for you. Moreover, it will help
                  you to manage all your transactions in one place.
                </p>
              </div>
            </div>
            <div className="col-lg-5">
              <div className="img-box">
                <img src={InfoImg} alt="info" />
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="custom-scroll-sec section left-side-img gradient-img">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 img-section">
              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>
            </div>
            <div className="col-lg-6">
              <div className="content-block">
                <h4 className="h4 title mb-3">
                  How To Monetize With Your DApp
                </h4>
                <p className="desc mb-3">
                  If we go back in the history then these crypto wallets were
                  only built for desktop versions, but as with the technology
                  advancement it began to expand as well. We have listed down
                  below the most used wallets currently in market right now.
                </p>
                <ul className="style-one">
                  <li>Transaction fees from users</li>
                  <li>Get your revenue with tokens</li>
                  <li>Set up membership/subscription plan for users</li>
                  <li>The good old way of earning through advertisements </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="process-wrap section">
        <div className="container">
          <h2 className="h2 title mb-5 text-center">
            Our Process of DApps Development
          </h2>

          <div className="process-wrap">
            <ul className="process-box">
              {Dapps_Data.map((item, index) => {
                return (
                  <>
                    <li className="d-flex">
                      <div className="title">
                        <span>{item.id}</span>
                        {item.title}
                      </div>
                      <div className="icon">
                        <div className="inner">
                          <img src={item.image} alt="icon" />
                        </div>
                      </div>
                      <div className="desc">{item.desc}</div>
                    </li>
                  </>
                );
              })}
            </ul>

            <p className="desc mt-3">
              You will have to rest assured about the issues that any user would
              have faced using a centralized application, or servers. DApps are
              completely reliable, and efficient and could handle multiple users
              in the network. That’s why DAO Platforms make sure that any
              product of them or clients works well by using the below approach.
              We at DAO Platforms follow the 4D approach to define any DApp
              Development Environment. If you are interested to know more about
              DAO Platform's work in DApps Development, then feel free to
              contact us. We are always here to help you build a better brand
              for yourself.
            </p>
          </div>
        </div>
      </section>

      <section className="quote-sec section">
        <div className="container">
          <div className="content">
            <h2 className="h2 title mb-4">
              "In my opinion, the future of mobile is the future of everything."
            </h2>

            <div className="author mb-4">
              - <strong>Peter Rojas</strong> (Co-founder, Engadget and Gizmodo)
            </div>

            <h3 className="subtitle ">
              Tech will transform from something we actively use to a more
              seamless integrated experience that is ‘on’ all the time.
            </h3>
          </div>
        </div>
      </section>

      <Ctablock />
    </>
  );
};

export default DAppsDevelopment;
