import React, { useEffect } from "react";
import Ctablock from "../components/Ctablock";

import InnerBanner from "../components/InnerBanner";
import InfoImg from "../images/info-sec-img.png";
import VideoImg from "../images/image-sec-img.png";
import ProcessImgIcon from "../images/icons/process-iconone.png";
import { Process_Block_Data } from "../data/all-data";

const NFTDevelopment = () => {
  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, []);
  return (
    <>
      <InnerBanner
        title="NFTBlockchain Development"
        desc="We build cryptocurrency wallet development services with security features and a user-friendly interface for users that matches their business model."
      />

      <section className="infosec-wrap section gray-bg">
        <div className="container">
          <div className="row">
            <div className="col-lg-7">
              <div className="content-block">
                <h2 className="h2 title mb-4">Art or Artist</h2>
                <p className="desc">
                  The trend of NFTs is emerging day by day, and because of that,
                  it can attract new users/customers in no time. We believe the
                  primary solution in NFTBlockchain development is launching an
                  NFT exchange network. Another thing you can look at is
                  building a marketplace and getting the trust of a community
                  for it.
                </p>
                <p className="des">
                  These NFTS are nothing but digital assets which hold a unique
                  value to the market and its holder. These Non-Fungible Tokens
                  can't be interchanged since the value it holds differs from
                  one person to another, and anyone can see their value of it on
                  the many NFTs-related websites.
                </p>
              </div>
            </div>
            <div className="col-lg-5">
              <div className="img-box">
                <img src={InfoImg} alt="info" />
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="custom-scroll-sec section left-side-img gradient-img">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 img-section">
              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>
            </div>
            <div className="col-lg-6">
              <div className="content-block">
                <h4 className="h4 title mb-3">
                  Industries We Work on for NFT Development Services
                </h4>
                <ul className="style-one">
                  <li>NFT for Artists</li>
                  <li>Music Industry</li>
                  <li>Food Industry</li>
                  <li>Real Estate Industry</li>
                  <li>Domain Names</li>
                  <li>Clothing Industry</li>
                  <li>Event Industry</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="infosec-two-wrap section gradient-img gray-bg ">
        <div className="container">
          <div className="row flex-column-reverse flex-lg-row">
            <div className="col-lg-7">
              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>
            </div>
            <div className="col-lg-5">
              <div className="content-block mb-4 m-lg-0">
                <h4 className="h4 title mb-3">Aspects of NFTs</h4>

                <ul className="style-one">
                  <li>
                    Scarcity - The developers of NFTs create rarity based on the
                    value and limitation of NFTs in the market.
                  </li>
                  <li>
                    One of a kind - As the NFTs are unique, so there are not
                    going to be similar-looking tokens in the entire world. You
                    can look for yourself.
                  </li>
                  <li>
                    Transferable- These cool NFTs can be found on the
                    marketplace, so any user around the world either buys or
                    sells or both from the same website.
                  </li>
                  <li>
                    NFTs' Rights - NFTs have their digital signature, and it is
                    stored in the Blockchain network. So, when any other person
                    buys NFT from me than the rights only would change.{" "}
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="quote-sec section">
        <div className="container">
          <div className="content">
            <h2 className="h2 title mb-4">
              “I think of an NFT as - it's as general a concept as the webpage.
              When it first comes out, people are like, "Oh it's a website" and
              they try to map it to the offline world. But it's a core new
              concept, the idea of owning something on the internet.”
            </h2>

            <div className="author mb-4">
              - <strong>Chris Dixon</strong> (Entrepreneur)
            </div>
          </div>
        </div>
      </section>

      <Ctablock />
    </>
  );
};

export default NFTDevelopment;
