import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Header from "./Header";
import Home from "../pages/Home";
import SiteFooter from "./SiteFooter";
import ModularPage from "../pages/ModulerPage";
import ContactUs from "../pages/ContactUs";
import WalletDevelopment from "../pages/WalletDevelopment";
import DAppsDevelopment from "../pages/DAppsDevelopment";
import LaunchpadDevelopment from "../pages/LaunchpadDevelopment";
import DefiSolutions from "../pages/DefiSolutions";
import ExchangeDevelopment from "../pages/ExchangeDevelopment";
import NFTDevelopment from "../pages/NFTBlockchainDevelopment";
import SmartDev from "../pages/SmartContractDevelopment";
const RouterInfo = () => {
  return (
    <>
      <Router basename={process.env.PUBLIC_URL}>
        <Header />
        <Routes>
          <Route exact path="/" element={<Home />} />
        </Routes>

        <Routes>
          <Route path="/work" element={<ModularPage />} />
        </Routes>

        <Routes>
          <Route path="/contact" element={<ContactUs />} />
        </Routes>

        <Routes>
          <Route path="/smartdev" element={<SmartDev />} />
        </Routes>

        <Routes>
          <Route path="/walletdev" element={<WalletDevelopment />} />
        </Routes>

        <Routes>
          <Route path="/dappsdev" element={<DAppsDevelopment />} />
        </Routes>

        <Routes>
          <Route path="/launchdev" element={<LaunchpadDevelopment />} />
        </Routes>

        <Routes>
          <Route path="/defisolu" element={<DefiSolutions />} />
        </Routes>

        <Routes>
          <Route path="/exchangedev" element={<ExchangeDevelopment />} />
        </Routes>

        <Routes>
          <Route path="/nftdev" element={<NFTDevelopment />} />
        </Routes>

        <SiteFooter />
      </Router>
    </>
  );
};

export default RouterInfo;
