import React, { useEffect } from "react";
import Ctablock from "../components/Ctablock";
import InnerBanner from "../components/InnerBanner";
import InfoImg from "../images/info-sec-img.png";

const DefiSolutions = () => {
  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, []);
  return (
    <>
      <InnerBanner
        title="DeFi Solutions"
        desc="Blockchain app development services"
      />

      <section className="infosec-wrap section gray-bg">
        <div className="container">
          <div className="row">
            <div className="col-lg-7">
              <div className="content-block">
                <h2 className="h2 title mb-4">Welcome To The Future</h2>
                <p className="desc">
                  Old times are gone when we have to be dependent on Banks for
                  handling our monetary transactions. Not only that approach is
                  complex but it also requires a lot of information like social
                  security number, proof of address, a government-issued ID, and
                  other such kinds of documents being used to set up a bank
                  account. Overall. it leads to a poor user experience and a
                  tedious task to manage all of these things.
                </p>

                <p className="desc">
                  While DAO Platform believes to invest yourself in this modern
                  approach known as Decentralized Finance (DeFi). It basically
                  runs on a decentralized network that is built on Ethereum
                  Blockchain, so the drawbacks of the old method are gone for
                  the good.
                </p>
              </div>
            </div>
            <div className="col-lg-5">
              <div className="img-box">
                <img src={InfoImg} alt="info" />
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="custom-scroll-sec section left-side-img gradient-img">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 img-section">
              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>

              <div className="img-box">
                <img src={InfoImg} alt="img" />
              </div>
            </div>
            <div className="col-lg-6">
              <div className="content-block">
                <h4 className="h4 title mb-3">
                  Benefits of using DeFi Solutions
                </h4>

                <ul className="style-one">
                  <li>Better Services</li>
                  <li>Automation Of Transactions</li>
                  <li>Fast Transactions</li>
                  <li>Efficient Enough</li>
                  <li>Great Security and Transparency</li>
                  <li>Vacate Technical Setbacks</li>
                </ul>

                <p className="desc mt-3">
                  If you are interested to know more about DAO Platform's work
                  in DeFi Solutions, then feel free to contact us. We are always
                  here to help you build a better brand for yourself.
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="quote-sec section">
        <div className="container">
          <div className="content">
            <h2 className="h2 title mb-4">
              “We are a stone’s throw away from the global financial industry
              running on common software infrastructure."
            </h2>

            <div className="author mb-4">
              - <strong>Lex Sokolin</strong> (Global Fintech Co-Head, ConsenSys)
            </div>
          </div>
        </div>
      </section>

      <Ctablock />
    </>
  );
};

export default DefiSolutions;
